/*================ Flex ================*/

.o-flex {
    box: horizontal row;
    flex-wrap: wrap;
}

.o-flex__item {
    flex: 1;
    order: 1;
    line-height: 0;

    @media(--mobile-max) {
        flex-basis: 100%;
    }
}

.o-flex--center {
    justify-content: center;
    text-align: center;
    display: flex;
    align-items: center;
}

.o-flex__item--reverse {
    order: 2;

    @media(--mobile-max) {
        order: 1;
    }
}

.o-flex__item--reverse-col1 {
    order: 1;

    @media(--tablet) {
        order: 2;
    }
}

.o-flex__item__is-left {
    order: 1;
}

.o-flex__item--is-right {
    order: 1;

    @media(--mobile-large) {
        order: 2;
    }
}

.o-flex__col-6 {
    flex-basis: 100%;
    @media(--mobile-large) {
        flex-basis: 50%;
    }
}

.o-flex__col-lg-1 {
    flex-basis: 100%;
    @media(--tablet) {
        flex-basis: 8.33%;
    }
}

