.c-iconSection {
    padding: 50px *;
    text-align: center;

    @media(--tablet-large){
        padding:100px *;
    }
}

.c-icons li {
    display:inline-block;
    margin:* 10px;
    font-size:12px;

    @media(--tablet){
        font-size:16px;
        margin:* 20px;
    }

    img,
    svg {
        max-width:30px;
        display:block;
        height:auto;

        @media(--tablet){
            max-width:60px;
        }
    }
}


.c-icons li img {
    max-width:60px;
    display:block;
    height:auto;

    @media(--mobile-max){
        max-width:30px;
    }
}